import React from 'react';
import { useShoppingContext } from '../context/ShoppingContext';

const Header = () => {
  const { activeTab } = useShoppingContext();
  
  //      
  const getTitle = () => {
    switch (activeTab) {
      case 'lists':
        return '  ';
      case 'create':
        return '  ';
      case 'recommendations':
        return ' ';
      case 'history':
        return ' ';
      default:
        return '';
    }
  };
  
  return (
    <header className="header">
      <h1>{getTitle()}</h1>
    </header>
  );
};

export default Header;
