/**
 *    
 * @param {string} query -   
 * @returns {Array} -  
 */
export const getSuggestions = (query) => {
  if (!query || query.length < 2) return [];
  
  const lowerQuery = query.toLowerCase();
  return productSuggestions
    .filter(product => product.toLowerCase().startsWith(lowerQuery))
    .slice(0, 5); //   
};